<template>
  <section class="mx-auto" style="width: 300px">
    <select
      class="form-select form-select-lg mb-3 border border-primary border-4"
      aria-label="Large select example"
      v-model="tempClass"
    >
      <option selected disabled value="false"></option>
      <option value="112">112</option>
      <option value="112B">112B</option>
      <option value="112C">112C</option>
    </select>
  </section>
  <div class="d-grid gap-2 col-6 mx-auto">
    <button
      class="btn btn-heightlight text-light btn-lg py-3"
      :disabled="!tempClass"
      @click="classSelect"
    >
      
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tempClass: false,
    };
  },
  emits: ['getClassData'],
  methods: {
    classSelect() {
      this.$emit('getClassData', this.tempClass);
    },
  },
};
</script>
