<template>
  <div>
    <h5 class="text-primary">{{ className }}</h5>
  </div>
  <SelectClass @getClassData="changeStatus" v-if="status" />
  <SwiperTopic v-else />
</template>

<script>
import SelectClass from '@/components/topic/SelectClass.vue';
import SwiperTopic from '@/components/topic/SwiperModel.vue';

export default {
  data() {
    return {
      status: true,
      className: '',
    };
  },
  methods: {
    changeStatus(id) {
      this.status = !this.status;
      this.className = id;
    },
  },
  components: { SelectClass, SwiperTopic },
};
</script>
